<template>
  <div class="account">
    <toast4 id="toast4" v-show="toastShow" style="top:300px"></toast4>
    <!--  -->
    <div class="aside">
      <ul>
        <li v-for="(item,index) in asideData.items" :class="{active:asideData.activeIndex==index}" @mouseover="asideData.activeIndex=index"
          @mouseout="asideData.activeIndex=1" @click="asideData.activeIndex=1">
          <router-link :to="item.path">{{item.name}}</router-link>
        </li>
      </ul>
    </div>
    <div class="content">
      <div class="title"></div>
      <div class="header">
        <div class="left">
          <div class="left_top">
            <label for="">:</label>
            <div class="img" v-show="header.headerEditShow">
              <img :src="userData.headUrl" alt="">
            </div>
            <div class="imgUpload" v-show="!header.headerEditShow">
              <img id="img" src="" alt="">
            </div>
          </div>
          <div class="left_foot">
            <p>jpgpngjpegbmp5M</p>
          </div>
        </div>
        <div class="right">
          <label for="file" class="modifyImg" v-show="header.headerEditShow"></label>
          <input type="file" id="file" name="" @change="preview" style="display: none" />
          <a v-show="!header.headerEditShow" @click="uplodHeadImg"></a>
          <a v-show="header.headerEditShow" @click="exit"></a>
          <a v-show="!header.headerEditShow"></a>
        </div>
      </div>
      <div class="line"></div>
      <div class="name">
        <div class="left">
          <label for="">{{userData.nickname}}</label>
          <p>714</p>
        </div>
        <div class="right">
          <a @click="name.nameShow = true"></a>
        </div>
      </div>
      <div class="line"></div>
      <div class="phone">
        <div class="left">
          <label for="">: {{userData.mobile}}</label>
          <p></p>
        </div>
        <div class="right">
          <a @click="phone.phoneShow = true"></a>
        </div>
      </div>
      <div class="line"></div>
      <div class="password">
        <div class="left">
          <label for="">
            <span style="font-size:16px"></span>
          </label>
          <p>6-14</p>
        </div>
        <div class="right">
          <a @click="password.passwordShow = true"></a>
        </div>
      </div>

      <!--  -->
      <div class="nameModify" v-show="name.nameShow">
        <div class="main">
          <img src="./close.png" alt="" class="close" @click="name.nameShow=false">
          <div class="name_title"></div>
          <div class="line"></div>
          <div class="name_name">{{userData.nickname}}</div>
          <div class="name_phone">{{userData.mobile}}</div>
          <div class="name_modify">
            <label for="">:</label>
            <input type="text" v-model="name.newName">
            <div class="info">
              <p v-show="name.verifyNameShow">714</p>
            </div>
          </div>
          <div class="btn">
            <button type="button" class="cancel" @click="name.nameShow=false"></button>
            <button type="button" class="sure" @click="verifyName"></button>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="phoneModify" v-show="phone.phoneShow">
        <div class="main">
          <img src="./close.png" alt="" class="close" @click="phone.phoneShow=false">
          <div class="name_title"></div>
          <div class="line"></div>
          <div class="name_name">{{userData.nickname}}</div>
          <div class="name_phone">{{userData.mobile}}</div>
          <div class="phone_modify">
            <label for="">:</label>
            <input type="text" v-model="phone.newPhone">
            <div class="info">
              <p v-show="phone.verifyPhoneShow">11</p>
            </div>
          </div>
          <div class="phone_checkNum">
            <label for="">:</label>
            <input type="text" v-model="phone.checkNum">
            <button type="button" @click="verifyPhone" v-show="phone.checkBtnShow"></button>
            <button type="button" v-show="!phone.checkBtnShow">{{phone.time}}s</button>
            <div class="info">
              <p v-show="phone.checkNumShow"></p>
            </div>
          </div>
          <div class="btn">
            <button type="button" class="cancel" @click="phone.phoneShow=false"></button>
            <button type="button" class="sure" @click="modifyPhone"></button>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="passwordModify" v-show="password.passwordShow">
        <div class="main">
          <img src="./close.png" alt="" class="close" @click="password.passwordShow=false">
          <div class="name_title"></div>
          <div class="line"></div>
          <div class="name_name">{{userData.nickname}}</div>
          <div class="name_phone">{{userData.mobile}}</div>
          <div class="name_modify">
            <label for="">:</label>
            <input type="text" v-model="password.originalPsd">
            <div class="info">
              <p v-show="password.originalPsdShow"></p>
            </div>
          </div>
          <div class="name_modify">
            <label for="">:</label>
            <input type="text" v-model="password.newPsd" @blur="verifyPsd('newPsd')">
            <div class="info" id="newPsd">
              <p v-show="password.newPsdShow">6-14</p>
            </div>
          </div>
          <div class="name_modify">
            <label for="">:</label>
            <input type="text" v-model="password.newPsdAgain" @blur="verifyPsd('newPsdAgain')">
            <div class="info">
              <p v-show="password.newPsdAgainShow"></p>
            </div>
          </div>
          <div class="btn">
            <button type="button" class="cancel" @click="password.passwordShow=false"></button>
            <button type="button" class="sure" @click="verifyPsd()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import toast4 from '../toast/toast'
 //   import Jcrop from 'jcrop'
  export default {
    components: {
      toast4
    },
    data() {
      return {
        // 
        asideData: {
          items: [{
              name: "",
              path: "./myEarnings"
            },
            {
              name: "",
              path: "./account"
            },
            {
              name: "",
              path: "./feedBack"
            }
          ],
          activeIndex: 1
        },
        //  
        userData: {
          mobile: '',
          nickname: '',
          headUrl: ''
        },
        header: {
          //
          headerEditShow: true, //
          fileImg: ''
        },
        name: {
          // 
          nameShow: false, //
          newName: "", //
          verifyNameShow: false //
        },
        phone: {
          //
          phoneShow: false, //
          newPhone: "", //
          checkNum: "", //
          verifyPhoneShow: false, //
          checkNumShow: false, //
          checkBtnShow: true, //
          time: 30
        },
        password: {
          //
          passwordShow: false, //
          originalPsd: "", //
          newPsd: "", //
          newPsdAgain: "", //
          originalPsdShow: false,
          newPsdShow: false,
          newPsdAgainShow: false
        },
        toastShow: false,
        src: './head.png'
      };
    },
    watch: {
      name: {
        handler(curVal, oldVal) {
          //   console.log(curVal.nameShow)
          if (curVal.nameShow == false) {
            this.name.newName = "",
              this.name.verifyNameShow = false
          }
        },
        deep: true
      },
      phone: {
        handler(curVal, oldVal) {
          //   console.log(curVal.nameShow)
          if (curVal.phoneShow == false) {
            this.phone.newPhone = ""; //
            this.phone.checkNum = ""; //
            this.phone.verifyPhoneShow = false;
            this.phone.checkNumShow = false
          }
        },
        deep: true
      },
      password: {
        handler(curVal, oldVal) {
          //   console.log(curVal.nameShow)
          if (curVal.passwordShow == false) {
            this.password.originalPsd = ""; //
            this.password.newPsd = ""; //
            this.password.newPsdAgain = "" //
            this.password.originalPsdShow = false;
            this.password.newPsdShow = false;
            this.password.newPsdAgainShow = false
          }
        },
        deep: true
      },


    },
    created() {
      this.getUser()
    },
    methods: {
      // ======================================
      preview(e) {
        var file = e.target.files[0];
        var url = URL.createObjectURL(file);
        var size = file.size
        // console.log(size)
        if (size > 5120) {
          this.enterToast('5M')
          return
        }
        document.getElementById("img").setAttribute("src", url);
        this.header.headerEditShow = false;
        // this.headImg()
        // console.log(file,file.size)
      },
      //   
    //   headImg() {
    //     var api;
    //     document.getElementById("img").Jcrop({
    //       // start off with jcrop-light class
    //        bgColor: 'white',
    //        bgOpacity: 0.5
    //     }, function () {
    //       api = this;
    //       api.setSelect([130, 65, 130 + 350, 65 + 285]);
    //       api.setOptions({
    //         bgFade: true
    //       });
    //       api.ui.selection.addClass('jcrop-selection');
    //     });
    //   },
        //  
        uplodHeadImg(){
            // this.$indexServer.headUrl()
        },
      // ========================================================
      exit() {
        // ----------------
        var _this = this;
        this.$indexServer.exit()
          .then((res) => {
            if (res.data.code == 200) {
              _this.$router.push({
                path: '/',
                query: {
                  id: 0
                }
              });
              localStorage.clear();
            } else if (res.data.code == 4010) {
              _this.enterToast('')
            } else if (res.data.code == 4006) {
              _this.enterToast('')
            } else {
              _this.enterToast('')
            }
          })
      },
      // ===================================
      getUser() {
        var _this = this;
        this.$indexServer.user()
          .then((res) => {
            if (res.data.code == 200) {
              _this.userData = res.data.data              
            }else if(res.data.code==400){
              _this.enterToast('')
            } else {
              _this.enterToast('')
            }
          })
      },

      // =============================================================
      // 
      verifyName() {
        // 
        if (!/^[a-zA-Z_\u4e00-\u9fa5]+$/.test(this.name.newName)) {
          this.name.verifyNameShow = true;
          return;
        } else {
          this.name.verifyNameShow = false;
        }
        // 
        var realLength = 0;
        for (var i = 0; i < this.name.newName.length; i++) {
          var charCode = this.name.newName.charCodeAt(i);
          if (charCode >= 0 && charCode <= 128) {
            realLength += 1;
          } else {
            realLength += 2;
          }
          if (realLength > 14) {
            this.name.verifyNameShow = true;
            return
          } else {
            this.name.verifyNameShow = false;
          }
        }
        this.modifyName()
      },
      // 
      modifyName() {
        //  ----------------
        var _this = this;
        this.$indexServer.nickname(_this.name.newName)
          .then((res) => {
            if (res.data.code == 200) {
              _this.getUser();
              _this.name.nameShow = false
            }else if(res.data.code==400){
              _this.enterToast('')
            }  else {
              this.enterToast('')
            }
          })

      },
      // ==========================================================
      // 
      verifyPhone(cb) {
        if (!/^1[3|4|5|7|8][0-9]{9}$/.test(this.phone.newPhone)) {
          this.phone.verifyPhoneShow = true;
          return
        } else {
          this.phone.verifyPhoneShow = false;
          this.getCheck(); //
        }
      },
      // 
      getCheck() {
        var _this = this;
        // ------------------
        this.$indexServer.validateCode(_this.phone.newPhone)
          .then((res) => {
            if (res.data.code == 200) {
              _this.phone.checkBtnShow = false;
              var timer = setInterval(() => {
                _this.phone.time--;
                if (_this.phone.time <= 0) {
                  _this.phone.checkBtnShow = true;
                  _this.phone.time = 30;
                  clearInterval(timer);
                }
              }, 1000)
            }else if(res.data.code==400){
              _this.enterToast('')
            }  else {
              _this.enterToast('')
            }
          })
      },

      // 
      modifyPhone() {
        //  ------------
        var _this = this;
        this.$indexServer.mobile(_this.phone.newPhone, _this.phone.checkNum)
          .then((res) => {
            if (res.data.code == 200) {
              _this.getUser();
              _this.phone.phoneShow = false
            }else if(res.data.code==400){
              _this.enterToast('')
            }  else {
              this.enterToast('')
            }
          })
        // 
      },
      // =====================================================
      verifyPsd(msg) {
        if (msg === 'newPsd') {
          if (
            //   
            this.password.newPsd.indexOf(" ") !== -1 ||
            this.password.newPsd.length < 6 ||
            this.password.newPsd.length > 14
          ) {
            this.password.newPsdShow = true;
            return;
          } else {
            this.password.newPsdShow = false;
          }
        } else if (msg === 'newPsdAgain') {
          //   
          if (this.password.newPsdAgain != this.password.newPsd) {
            this.password.newPsdAgainShow = true;
            return
          } else {
            this.password.newPsdAgainShow = false;
          }
        } else {
          if (
            //   
            this.password.newPsd.indexOf(" ") !== -1 ||
            this.password.newPsd.length < 6 ||
            this.password.newPsd.length > 14
          ) {
            this.password.newPsdShow = true;
            return;
          } else {
            this.password.newPsdShow = false;
          }
          //   
          if (this.password.newPsdAgain != this.password.newPsd) {
            this.password.newPsdAgainShow = true;
            return
          } else {
            this.password.newPsdAgainShow = false;
            // 
            this.modifyPsd()
          }
        }

      },
      //  
      modifyPsd() {
        // --------------
        // alert(1)
        var _this = this;
        this.$indexServer.passwd(_this.password.originalPsd, _this.password.newPsd)
          .then((res) => {
            if (res.data.code == 200) {
              _this.password.passwordShow = false;
              _this.enterToast('')
            } else if (res.data.code == 4007) {
              _this.password.originalPsdSh = true
            }else if(res.data.code==400){
              _this.enterToast('')
            } else {
              _this.enterToast('')
            }
          })
      },
      enterToast(text, time) {
        var toast = document.getElementById("toast4");
        // console.log(toast)
        if (toast) {
          toast.innerHTML = text;
          this.toastShow = true;
          setTimeout(() => {
            this.toastShow = false;
          }, 2000 || time);
        }
      }

    }
  };

</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .account {
    padding: 0 15%;
  }

  .aside {
    height: 130px;
    width: 147px; // border: 1px solid #d2d2d2;
    position: fixed;
    top: 129px;
    left: 3.5%;
    box-shadow: 0px 0px 10px #D6D6D6;

    ul {
      height: 100%;
      width: 100%;
      display: flex;
      display: -webkit-flex;
      flex-direction: column;

      li {
        flex: 1;
        border-bottom: 1px solid rgb(210, 210, 210);
        background-color: #ffffff;

        a {
          font-family: 'PingFangSC-Regular';
          font-size: 18px;
          color: rgb(1, 0, 25);
          display: flex;
          display: -webkit-flex;
          justify-content: center;
          align-items: center;
          height: 100%;
          width: 100%;
        }
      }

      li:last-child {
        border: none;
      }

      .active {
        background-color: rgb(249, 85, 63);
        transition: background 0.5s;

        a {
          color: #ffffff;
        }
      }
    }
  }

  .content {
    margin: 21px 0 28px 0;
    min-width: 950px;
    background-color: #fff;
    font-family: PingFangSC-Regular;
    box-shadow: 0px 0px 10px #D6D6D6;

    .title {
      height: 90px;
      background-color: #DDDDDD;
      padding: 39px 0 0 48px;
      box-sizing: border-box;
      font-size: 24px;
      color: rgb(0, 1, 32);
    } // 
    .line {
      height: 1px;
      width: 90%;
      margin: 0 auto 41px;
      background-color: rgb(220, 220, 220);
    }

    .header {
      overflow: hidden;

      .left {
        float: left;

        .left_top {
          overflow: hidden;
          margin-left: 91px;

          label {
            font-size: 20px;
            color: rgb(0, 1, 32);
            float: left;
            margin-top: 84px;
          }

          .img {
            height: 112px;
            width: 112px;
            margin: 30px 0 29px 22px;
            float: left;

            img {
              width: 100%;
              height: 100%;
            }
          }

          .imgUpload {
            // height: 138px;
            // width: 138px;
            margin: 30px 0 29px 13px;
            float: left;
            background-color: pink;
          }
        }

        .left_foot {
          p {
            font-size: 16px;
            color: rgb(170, 170, 170);
            margin: 0 0 16px 100px;
          }
        }
      }

      .right {
        float: right;
        margin-right: 86px;
        margin-top: 81px;

        .modifyImg {
          cursor: pointer;
          font-size: 18px;
          color: rgb(249, 85, 63);
          background: url('./edit.png') no-repeat left;
          padding-left: 28px;
        }

        a {
          cursor: pointer;
          font-size: 18px;
          color: rgb(249, 85, 63);
        }

        a:nth-child(4) {
          margin-left: 31px;
        }

        a:nth-child(5) {
          margin-left: 31px;
          margin-right: 38px;
        }
      }
    }

    .name,
    .phone,
    .password {
      overflow: hidden;

      .left {
        float: left;
        margin-left: 92px;

        label {
          font-size: 20px;
          color: rgb(0, 1, 32);
        }

        p {
          font-size: 16px;
          color: rgb(170, 170, 170);
          margin: 12px 0 35px 0;
        }
      }

      .right {
        float: right;

        a {
          cursor: pointer;
          font-size: 18px;
          color: rgb(249, 85, 63);
          background: url('./edit.png') no-repeat left;
          padding: 0 194px 0 28px;
        }
      }
    } // 
    .nameModify,
    .phoneModify,
    .passwordModify {
      height: 100%;
      width: 100%;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.3);

      .main {
        height: 354px;
        width: 537px;
        background-color: #fff;
        position: absolute;
        margin: auto;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        .close {
          position: absolute;
          top: -28px;
          right: -28px;
        }

        .name_title {
          font-size: 20px;
          color: rgb(0, 1, 32);
          text-align: center;
          padding: 32px 0;
        }

        .line {
          margin: 0 auto;
        }

        .name_name,
        .name_phone {
          font-size: 18px;
          color: rgb(87, 87, 87);
          padding: 29px 0 21px 61px;
        }

        .name_phone {
          padding: 0 0 23px 61px;
        }

        .name_modify {
          padding-left: 61px;
          font-size: 18px;
          color: rgb(87, 87, 87);

          input {
            height: 46px;
            width: 303px;
            border: 1px solid rgb(229, 229, 229);
            outline: none;
            margin-left: 15px;
            padding-left: 8px;
            box-sizing: border-box;
          }

          .info {
            height: 40px;
            overflow: hidden;

            p {
              width: 303px;
              font-size: 12px;
              color: rgb(249, 85, 63);
              margin: 9px 0 7px 116px;
              padding-left: 15px;
              background: url('./mark.png') no-repeat left top;
              box-sizing: border-box;
            }
          }
        }

        .btn {
          display: flex;
          justify-content: center;

          button {
            height: 46px;
            width: 201px;
            border: none;
            outline: none;
            color: #ffffff;
            font-size: 16px;
            font-family: 'AdobeHeltlStd_regular';
          }

          .cancel {
            background-color: rgb(179, 179, 179);
            margin-right: 10px;
          }

          .sure {
            background-color: rgb(249, 85, 63);
          }
        }
      }
    } // 
    .phoneModify {
      .main {
        height: 408px;

        .phone_modify,
        .phone_checkNum {
          padding-left: 61px;
          font-size: 18px;
          color: rgb(87, 87, 87);

          input {
            height: 46px;
            width: 312px; // width 165px
            border: 1px solid rgb(229, 229, 229);
            outline: none;
            margin-left: 15px;
            padding-left: 8px;
            box-sizing: border-box;
          }

          .info {
            height: 25px;

            p {
              // width 303px
              line-height: 25px;
              font-size: 12px;
              color: rgb(249, 85, 63);
              margin-left: 99px;
              padding-left: 15px;
              background: url('./mark.png') no-repeat left center;
            }
          }
        }

        .phone_checkNum {
          padding-left: 78px;
          input {
            width: 165px;
          }

          button {
            height: 46px;
            width: 145px;
            outline: none;
            border: none;
            background-color: rgb(249, 85, 63);
            color: #fff;
            font-size: 16px;
          }
        }
      }
    } // 
    .passwordModify {
      .main {
        height: 503px;

        label {
          display: inline-block;
          width: 95px;
        }

        .name_modify {
          .info {
            height: 25px;
          }
        }

        #newPsd {
          height: 40px;
        }

        .btn {
          margin-top: 15px;
        }
      }
    }
  }

</style>



