<template>
  <div class="loginVue">
    <div class="container">
      <!-- <toast id="toast" v-show="toastShow" style="top:340px"></toast>                     -->
      <div class="head">
        <ul>
          <li :class="{active: mobilePsd==true}" @click="mobilePsd=true"></li>
          <li :class="{active: mobilePsd==false}" @click="mobilePsd=false"></li>
        </ul>
      </div>
      <div class="mobile_main" v-show = 'mobilePsd'>
        <div class="phone">
          <label for=""></label>
          <input type="text" class="phoneNum" v-model="mobileMain.phone" @blur="validate('phone_main')">
          <div class="warn">
            <p v-show="phoneWarn.warn3"></p>
            <p v-show="phoneWarn.warn4">11</p>
          </div>
        </div>
        <div class="setPassword">
          <label for=""></label>
          <input type="password" v-model="mobileMain.password">
          <router-link class="getPassword" to="/getPassword">?</router-link>          
        </div>
        <div class="login">
          <div class="warn">
            <p v-show="phoneWarn.warn1"></p>
            <p v-show="phoneWarn.warn2"></p>
            <p v-show="phoneWarn.warn7"></p>            
            <p v-show="toastShow1"></p>            
            <p v-show="toastShow2"></p>            
          </div>
          <button type="button" @click="login"></button>
        </div>
        <div class="loginState">
          <input type="checkbox" value='false' v-model="state" @click="state=!state">
        </div>
        <div class="toRegister">
          <span></span>
        </div>
        <div class="created">
          <router-link class="goRegister" to="../register"></router-link>
        </div>
      </div>
      <div class="fast_main" v-show = '!mobilePsd'>
         <div class="phone">
          <label for=""></label>
          <input type="text" class="phoneNum" v-model="fastMain.phone" @blur="validate('phone_fast')">
          <button type="button" v-show="fastMain.checkBtn" @click="getChecknum"></button>
          <button type="button" v-show="!fastMain.checkBtn" @click="getChecknum">{{fastMain.time}}s</button>
          <div class="warn">
            <p v-show="phoneWarn.warn5"></p>
            <p v-show="phoneWarn.warn6">11</p>
            <p v-show="toastShow4"></p>
          </div>
        </div>
        <div class="password">
          <label></label>
          <input type="text" class="passwordNum" placeholder="" v-model="fastMain.password">
          <div class="warn">
            <p v-show="fastMain.warn1"></p>
            <p v-show="fastMain.warn2">,</p>
            <p v-show="toastShow3"></p>
          </div>
        </div>

        <div class="login">
          <button type="button" @click="fastLogin"></button>
        </div>
        <div class="agreement">
          <div></div>
          <div><router-link class="agrreTo" to="/agreement"></router-link></div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
// import toast from '../toast/toast'
export default {
  // components:{toast}, 
  data() {
    return {
      mobilePsd: true,//truefalse
      mobileMain: {
        phone: '',
        password: ''
      },
      phoneWarn:{
        warn1:false,//
        warn2:false,//
        warn3:false,//--
        warn4:false,//--
        warn5:false,//--
        warn6:false,//--
        warn7:false,//---
      },
      fastMain:{
        phone: '',
        password: '',
        warn1:false,
        warn2:false,
        checkBtn:true,
        time:30
      },      
      toastShow1:false,//--
      toastShow2:false,//--
      toastShow3:false,//--
      toastShow4:false,//--
      toastShow:false,
      state:false,//
    };
  },
  watch:{
    $route:{
      handler(curVal,oldVal){
        // console.log(curVal)
        if(curVal.fullPath!='/Login'){
          this.fastMain.phone='';
          this.fastMain.password='';
          this.mobileMain.phone='';
          this.mobileMain.password=''
        }        
      },
      deep:true
    },
  },
  methods: {
    // 
    validate (info) {
        // ---
      if(info==='phone_main'){
        if (this.mobileMain.phone.length === 0) {
          this.phoneWarn.warn3 = true
          this.phoneWarn.warn4 = false
          return false
        } else if (/^1[3|4|5|7|8][0-9]\d{8}$/.test(this.mobileMain.phone)) {          
          this.phoneWarn.warn3 = false
          this.phoneWarn.warn4 = false
          return true
        } else {
          this.phoneWarn.warn3 = false
          this.phoneWarn.warn4 = true
          return false
        } 
      }
          // ---      
      if(info==='phone_fast'){
        if (this.fastMain.phone.length === 0) {
          this.phoneWarn.warn5 = true
          this.phoneWarn.warn6 = false
          return false
        } else if (/^1[3|4|5|7|8][0-9]\d{8}$/.test(this.fastMain.phone)) {
          this.phoneWarn.warn5 = false
          this.phoneWarn.warn6 = false
          return true
        } else {
          this.phoneWarn.warn5 = false
          this.phoneWarn.warn6 = true
          return false
        }
      }
        
    },
    // =========================================
    login() {
      var _this = this;
      this.toastShow21=false;
      this.toastShow2=false;
      // 
      if(this.validate('phone_main')){
         // 
        if(this.mobileMain.password.length==0){
          this.phoneWarn.warn7 = true
          return
        }else{
          this.phoneWarn.warn7 = false
        }
         // ----------------
        this.$indexServer.loginByPassword (_this.mobileMain.phone, _this.mobileMain.password)
        .then((res)=>{
          // console.log(res.data)
          if(res.data.code==200){
            _this.$router.push({path:'/',query:{id:1}});
            localStorage.setItem("nickname",res.data.data.nickname);
            localStorage.setItem("headUrl",res.data.data.headUrl); 
            _this.remState()         
          }else if(res.data.code==4001||res.data.code==4007||res.data.code==4008){
            _this.phoneWarn.warn1=true;
            _this.phoneWarn.warn2=false;
          }else if(res.data.code==4006){
            _this.toastShow1=true;          
          }else if(res.data.code==40011){
            _this.phoneWarn.warn1=false;
            _this.phoneWarn.warn2=true;
          }else{
            this.toastShow2=true
          }
        })
      };
    },
    // cookie
    setCookie(name,value,day){
      var date = new Date();
      date.setDate(date.getDate() + day);
      document.cookie = name + '=' + value + ';expires='+ date;
    },   
    // cookie
    delCookie(name){
      this.setCookie(name,null,-1);
    },
    // 
    remState(){
      // console.log(this.state)
      var _this = this
      if(this.state==true){
        this.setCookie('phone',_this.mobileMain.phone,7); //cookie7
        this.setCookie('password',_this.mobileMain.password,7); //cookie7
      }else if(this.state==false){
        this.delCookie('phone');
        this.delCookie('password');
      }      
    },
    
    // ====================================
    // 
    getChecknum() {
      var _this = this;
      this.toastShow4=false      
      if(this.validate('phone_fast')){
        // ------------------
        this.$indexServer.validateCode(_this.fastMain.phone)
        .then((res)=>{
            if(res.data.code == 200){
              this.fastMain.checkBtn = false;//
              var timer = setInterval(()=>{
                this.fastMain.time--;
                if(this.fastMain.time<=0){
                  this.fastMain.checkBtn = true;
                  this.fastMain.time = 30;
                  clearInterval(timer);
                }
              },1000)
            }else{
              this.toastShow4=true
            }
        })
      } 
    },
    // 
    fastLogin() {
      var _this =this;
       _this.toastShow3=false
      if(this.validate('phone_fast')){
        if(this.fastMain.password.length==0){
          this.fastMain.warn1=true
          return
        }else{
          this.fastMain.warn1=false
        }
         // ------------------
        this.$indexServer.loginByValidateCode(this.fastMain.phone,this.fastMain.password)
        .then((res)=>{
          if(res.data.code==200){
            _this.$router.push({path:'/',query:{id:1}});
            localStorage.setItem("nickname",res.data.data.nickname);
            localStorage.setItem("headUrl",res.data.data.headUrl);
            _this.mobilePsd=true
          }else if(res.data.code==4000||res.data.code==4005||res.data.code==4008||res.data.code==4009){
            _this.fastMain.warn2 = true;
          }else{
            _this.toastShow3=true
          }
        })
      }     
    },
    // enterToast(text,time) {
    //   var toast = document.getElementById("toast");
    //   // console.log(toast)
    //   if (toast) {
    //     toast.innerHTML = text;
    //     this.toastShow = true;
    //     setTimeout(() => {
    //       this.toastShow = false;
    //     }, 2000||time);
    //   }
    // }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
.container {
  // height: 550px;
  width: 632px;
  box-shadow: 0px 0px 10px #D6D6D6;
  margin: 90px auto 176px;
  font-family: 'PingFangSC-Reguler';
  background-color: #fff;
  color: rgb(1, 0, 25);

  .active {
    background-color: #ffffff;
    color: rgb(1, 0, 25);
  }

  .head {
    width: 100%;
    height: 75px;
    color: #ffffff;
    font-size: 24px;

    ul {
      height: 100%;
      background-color: rgb(195, 195, 195);

      li {
        float: left;
        width: 50%;
        height: 100%;
        line-height: 75px;
        text-align: center;
      }
    }
  }

  .mobile_main, .fast_main {
    // width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .phone, .password, .login {
      label {
        font-size: 18px;
      }

      input {
        height: 46px;
        border: 1px solid rgb(195, 195, 195);
        outline: none;
        box-sizing: border-box;
        margin: 0 0 25px 10px;
        padding-left: 8px;
      }

      button {
        height: 46px;
        // cursor: pointer;
        border: none;
        outline: none;
        background-color: rgb(249, 85, 63);
        font-size: 18px;
        color: #ffffff;
        font-family: 'AdobeHeitiStd-Regular';
      }

      p {
        background: url('./note.png') no-repeat left;
        background-size: auto 100%;
        padding-left: 20px;
        font-size: 12px;
        color: rgb(249, 85, 63);
      }
    }
  }

  .mobile_main {
    margin-top: 35px;
    margin-right: 104px;

    .phone, .setPassword {
      position relative
      input {
        width: 333px;
        height: 46px;
        border: 1px solid rgb(195, 195, 195);
        outline: none;
        margin: 0px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 10px;
      }
      .getPassword {
        position: absolute;
        top: 50px;
        right: 0;
        font-size: 12px;
        color: rgb(112, 112, 112);
      }
      
    }

    .login {
      button {
        margin-top 10px        
        width: 420px;
      }
      
    }

    .loginState {
      display: flex;
      margin-top: 16px;
      font-size: 12px;
      color: rgb(0, 1, 32);

      input {
        margin-right: 5px;
      }
    }

    .toRegister {
      margin: 32px 0 27px 0;
      text-align: center;
      width: 80%;

      span {
        display: block;
        position: relative;
        margin-right: -20px;
        font-size: 18px;
        color: rgb(128, 128, 128);
      }

      span:before, span:after {
        content: '';
        position: absolute;
        top: 52%;
        background: #cccccc;
        width: 35%;
        height: 1px;
      }

      span:before {
        left: -25px;
      }

      span:after {
        right: 0;
      }
    }

    .created {
      margin-bottom: 47px;

      .goRegister {
        display: block;
        background-color: rgb(192, 192, 192);
        height: 46px;
        width: 420px;
        text-align: center;
        line-height: 46px;
        // cursor: pointer;
        border: none;
        outline: none;
        font-size: 18px;
        color: #ffffff;
        font-family: 'AdobeHeitiStd-Regular';
      }
    }
    .warn {
      height: 25px;
      padding: 5px 0 0 87px;
      box-sizing: border-box;
    }
    
    
  }

  .fast_main {
    margin-right: 104px;
    margin-top: 54px;

    .phone {
      input {
        width: 187px;
        margin-right: 6px;
      }

      button {
        width: 136px;
      }
      input{
        margin 0px
      }
      
    }

    .password {
      position: relative;
      input {
        width: 328px;
        margin: 0px;
      }

     
    }
    .warn {
      height: 25px;
      padding: 5px 0 0 78px;
      box-sizing: border-box;
    }

    .login {
      button {
        width: 420px;
      }
    }

    .agreement {
      font-size: 12px;
      color: rgb(112, 112, 112);

      div:first-child {
        margin: 23px 0 8px 0;
      }

      div:nth-child(2) {
        text-align: right;
        margin: 0 0 47px 0;

        .agrreTo {
          color: #FF543B;
        }
      }
    }
  }
}
</style>



