<template>
  <div class="components-container">
    <div class="info">UE<br>UEdefaultMsgconfig()</div>
    <button @click="getUEContent()"></button>
    <div class="editor-container">
      <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
      <UE :defaultMsg=defaultMsg :config=config :id=ue2 ref="ue2"></UE>
    </div>
  </div>
</template>
<style>
  .info{
    border-radius: 10px;
    line-height: 20px;
    padding: 10px;
    margin: 10px;
    background-color: #ffffff;
  }
</style>
<script>
  import UE from '../../components/ue/ue.vue';
  export default {
    components: {UE},
    data() {
      return {
        defaultMsg: 'UE',
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 350
        },
        ue1: "ue1", // id
        ue2: "ue2"
      }
    },
    methods: {
      getUEContent() {
        let content = this.$refs.ue.getUEContent(); // 
        this.$notify({
          title: '',
          message: content,
          type: 'success'
        });
        console.log(content)
      }
    }
  };
</script>






